// @flow
import Collection from '../../api/Collection'
import type { CollectionOptions } from '../../api/types'

class LayoutConfigsStore extends Collection {

  constructor (appId: string, options?: CollectionOptions = {}) {
    const url = `/odata/LayoutConfigs`
    super(appId, url, options)
  }
}

export function createLayoutConfigsStore (appId: string, options?: CollectionOptions = {}) {
  return new LayoutConfigsStore(appId, options)
}



