// @flow
import Collection from '../../api/Collection'
import type { CollectionOptions } from '../../api/types'

class ViewConfigsStore extends Collection {

  constructor (appId: string, options?: CollectionOptions = {}) {
    const url = `/odata/ViewConfigs`
    super(appId, url, options)
  }
}

export function createViewConfigsStore (appId: string, options?: CollectionOptions = {}) {
  return new ViewConfigsStore(appId, options)
}



