// @flow
import Collection from '../../api/Collection'
import type { CollectionOptions } from '../../api/types'

class ChartConfigsStore extends Collection {

  constructor (appId: string, options?: CollectionOptions = {}) {
    const url = `/odata/ChartConfigs`
    super(appId, url, options)
  }
}

export function createChartConfigsStore (appId: string, options?: CollectionOptions = {}) {
  return new ChartConfigsStore(appId, options)
}



