// @flow
import { createListModelManager, ListModelManager } from '../../api/ListModelManager'
import Collection from '../../api/Collection'
import type { CollectionOptions } from '../../api/types'

class ListItemsStore extends Collection {
  connectionId: string
  listId: string
  model: any = null
  promise: Promise<*>
  listModelManager: ListModelManager

  constructor (appId: string, connectionId: string, listId: string, options?: CollectionOptions = {}) {
    const url = `/odata/Connections('${connectionId}')/Lists('${listId}')/ListItems`
    super(appId, url, options)

    this.connectionId = connectionId
    this.listId = listId
    this.listModelManager = createListModelManager(appId)
    this.promise = this.listModelManager.getListModel(connectionId, listId)
      .then((model) => {
        this.model = model
      })
  }

  // allow getListModel() and read() to be performed in parallel
  read () {
    let readPromise = super.read()
    let promises = [ this.promise, readPromise ]
    this.isRequesting = true

    return Promise.all(promises)
      .then(() => {
        return this
      })
  }

  // Todo: Consider making add async by default
  add (item: {}) {
    if (!this.model) {
      return 'Model hasn\'t loaded yet.'
    }
    let defaultValues = this.model.defaultModelValues || {}
    let result = {}

    Object.keys(defaultValues).forEach(function (key) {
      result[ key ] = item.hasOwnProperty(key) ? item[ key ] : defaultValues[ key ]
    })

    return super.add(result)
  }

}
/**
 * Create a ListItemsStore instance
 */
export function createListItemsStore (appId: string, connectionId: string, listId: string, options?: CollectionOptions = {}): ListItemsStore {
  return new ListItemsStore(appId, connectionId, listId, options)
}



