// flow
import localforage from 'localforage'
import { DEFAULT_CONNECTION_ID } from './../constants'
import { action, extendObservable, toJS, autorun } from 'mobx'
import storesTransformer from './storesTransformer'
import * as stores from '../stores'

// Get app data from remote and store in localforage
class AppRequest {
  isRequesting: boolean
  parsedResponses: Object
  appId: string

  constructor (appId: string) {
    this.appId = appId
    this.rawResponses = {}
    this.parsedResponses = {}
    this.toJS = toJS

    this.lfStore = localforage.createInstance({
      name: `ccc-${appId}`
    })

    extendObservable(this, {
      isRequesting: true
    })

    // get all app data with the exception of ListItems
    this.stores = {
      actions: stores.createActionConfigsStore(appId, {
        top: -1
      }),
      app: stores.createApplicationsStore({
        expand: [ 'AppConfig($select=AppId,ApplicationName,Description,ModifiedBy,ModifiedDate)' ],
        top: -1,
        filter: {
          logic: 'and',
          filters: [
            { field: 'Id', operator: 'eq', value: appId }
          ]
        }
      }),
      charts: stores.createChartConfigsStore(appId, {
        top: -1
      }),
      connections: stores.createConnectionsStore(appId, {
        expand: [ 'Capabilities' ],
        top: -1
      }),
      grids: stores.createGridConfigsStore(appId, {
        top: -1
      }),
      layouts: stores.createLayoutConfigsStore(appId, {
        top: -1
      }),
      libraries: stores.createLibraryConfigsStore(appId, {
        top: -1
      }),
      lists: stores.createListsStore(appId, DEFAULT_CONNECTION_ID, {
        expand: [ 'Fields' ],
        top: -1
      }),
      views: stores.createViewConfigsStore(appId, {
        expand: [ 'Tabs($expand=Views)' ],
        top: -1
      })
    }

    autorun(() => {
      this.parsedResponses = storesTransformer(this.stores)
    })
  }

  read () {
    const promises = []

    return this.lfStore.getItem('responses')
      .then((responses) => {
        // Todo: Check for updates
        const stores = this.stores
        if (responses && responses.hasOwnProperty('app')) {
          Object.keys(stores).forEach(function (key) {
            if (responses[ key ]) {
              stores[ key ].processResponse(responses[ key ])
            }
          })
        } else {
          return getFromServer.call(this)
        }
      })
      .then(action(() => {
        this.isRequesting = false
        return this.parsedResponses
      }))

    function getFromServer () {
      const stores = this.stores
      Object.keys(stores).forEach(function (key) {
        promises.push(stores[ key ].read())
      })

      return Promise.all(promises)
        .then(() => {
          const stores = this.stores
          const rawResponses = this.rawResponses

          Object.keys(stores).forEach(function (key) {
            rawResponses[ key ] = stores[ key ].getPristineResponse()
          })
          this.lfStore.setItem(`last-saved`, new Date().toISOString())
          this.lfStore.setItem(`responses`, this.rawResponses)
        })
    }
  }
}

export function createAppRequest (appId: string) {
  return new AppRequest(appId)
}



