// @flow
import Collection from '../../api/Collection'
import type { CollectionOptions } from '../../api/types'

class FieldsStore extends Collection {
  connectionId: string
  listId: string

  constructor (appId: string, connectionId: string, listId: string, options?: CollectionOptions = {}) {
    const url = `/odata/Connections('${connectionId}')/Lists('${listId}')/Fields`
    super(appId, url, options)
    this.connectionId = connectionId
    this.listId = listId
  }
}

export function createFieldsStore (appId: string, connectionId: string, listId: string, options?: CollectionOptions = {}) {
  return new FieldsStore(appId, connectionId, listId, options)
}



