// @flow
import Collection from '../../api/Collection'
import type { CollectionOptions } from '../../api/types'

class LibraryConfigsStore extends Collection {

  constructor (appId: string, options?: CollectionOptions = {}) {
    const url = `/odata/LibraryConfigs`
    super(appId, url, options)
  }
}

export function createLibraryConfigsStore (appId: string, options?: CollectionOptions = {}) {
  return new LibraryConfigsStore(appId, options)
}



