// @flow
import { action, toJS, extendObservable, observable, ObservableMap, runInAction } from 'mobx'
import { createViewModel } from 'mobx-utils'
import { createTransport } from './Transport'
import { normalizeSort, normalizeArrayOrString, guid } from './fn/'
import _ from 'lodash'

import type { Sort, SortDir, SortExpression, FilterGroup, CollectionOptions } from './types'

export default class Collections {
  _pristineResponse: any;
  _pristineData: [];
  appId: string;
  baseUrl: string;
  transport: any;
  data: ObservableMap;
  isRequesting: boolean;
  isSilent: boolean;
  $nextLink: string;
  $orderby: Sort;
  $filter: {};
  $select: Array<string>;
  $expand: Array<string>;
  $top: number;
  parse: Function;
  serialize: Function;

  constructor (appId: string, url: string, options?: CollectionOptions = {}) {
    this.appId = appId
    this.baseUrl = url
    this.transport = createTransport(appId, url)
    this._pristineData = []

    // if (typeof options.parse === 'function') {
    //   this.parse = options.parse
    //   delete options.parse
    // }
    //
    // if (typeof options.serialize === 'function') {
    //   this.serialize = options.serialize
    //   delete options.serialize
    // }

    extendObservable(this, {
      data: [],
      isRequesting: false,
      isSilent: false,
      $nextLink: '',
      $filter: {},
      $orderby: [],
      $select: [],
      $expand: [],
      $top: 30,
      get hasChanges (): boolean {
        return this.data.findIndex(item => item.isDirty || item.isNew) > -1
      },
      get canReadMore (): boolean {
        // Todo: @Rick: Remove nextLink if there are no more items
        return this.$nextLink !== '' && this.data.length % this.$top === 0
      },
      add: action(this.add),
      expand: action(this.expand),
      select: action(this.select),
      orderby: action(this.orderby),
      filter: action(this.filter),
      top: action(this.top),
      query: action(this.query),
      read: action(this.read),
      readMore: action(this.readMore),
      remove: action(this.remove),
      sync: action(this.sync),
      processResponse: action(this.processResponse),
      // alias
      sort: action(this.orderby)
    })

    this.query(options, true)
  }

  /**
   * returns itemModel at specified position in instance.data
   * @returns {*} itemModel
   */
  at (pos: number): any | null {
    if (this.data.length < pos + 1) {
      return null
    }
    return this.data[ pos ]
  }

  add (item: {Id?: string, isNew?: boolean}): any {
    if (!item.hasOwnProperty('Id')) {
      Object.assign(item, {
        Id: guid(),
        isNew: true
      })
    }
    let model = createViewModel(observable(item))
    this.data.unshift(model)

    return model
  }

  /**
   * returns itemModel with specified GUID
   * @param id
   * @returns {null}
   */
  get (id: string): any | null {
    const item = this.data.filter((item) => item.Id === id)
    return item && item.length === 1 ? item[ 0 ] : null
  }

  getPristineResponse () {
    return this._pristineResponse
  }

  getPristineData () {
    return this._pristineData
  }

  expand (options?: Array<string> | string) {
    if (!options) {
      return toJS(this.$expand)
    }

    this.$expand = normalizeArrayOrString(options)
    this.data = []

    return this.read()
  }

  select (options?: Array<string> | string) {
    if (!options) {
      return toJS(this.$select)
    }

    this.$select = normalizeArrayOrString(options)
    this.data = []

    return this.read()
  }

  top (options?: number) {
    if (!options) {
      return toJS(this.$top)
    }

    this.$top = options
    this.data = []

    return this.read()
  }

  orderby (options?: Sort | SortExpression | string, dir?: SortDir = 'asc') {
    if (!options) {
      return toJS(this.$orderby)
    }
    this.$orderby = normalizeSort(options, dir)
    this.data = []

    return this.read()
  }

  filter (options?: FilterGroup) {
    if (!options) {
      return toJS(this.$filter)
    }
    this.$filter = options
    this.data = []

    return this.read()
  }

  query (options?: {} = {}, silent: boolean = false) {
    this.isSilent = silent
    const allowedOdataVerbs = [ 'select', 'orderby', 'sort', 'expand', 'top', 'filter' ]

    runInAction(() => {
      allowedOdataVerbs.forEach((key) => {
        if (key === 'sort') {
          key = 'orderby'
        }
        if (options && options.hasOwnProperty(key)) {
          // setting observable directly not calling methods
          // $FlowFixMe Class properties are not indexable https://github.com/facebook/flow/issues/1730
          this[ `$${key}` ] = (options[ key ])
        }
      })
    })

    if (this.isSilent) {
      this.isSilent = !this.isSilent
      return
    }

    return this.read()
  }

  parse (resp: any) {
    return resp
  }

  serialize (data: any) {
    return data
  }

  processResponse (resp: any) {
    this._pristineResponse = _.cloneDeep(resp)
    const data = _.isArray(resp.value) ? resp.value : [ resp.value ]
    const parsedData = this.parse(data)
    // append data
    this._pristineData = this._pristineData.concat(_.cloneDeep(parsedData))

    const nextLink = checkNextLink(resp)

    this.isRequesting = false
    this.$nextLink = nextLink

    const result = parsedData.map((item) => {
      return createViewModel(observable(item))
    })

    // append data in a single operation
    this.data = this.data.concat(result)

    // return the store
    return this

    // remove leading hostname/protocol
    function checkNextLink (response) {
      const link = response ? response[ '@odata.nextLink' ] : ''

      if (link) {
        return link.split('?')[ 1 ]
      }

      return ''
    }
  }

  read (): Promise<*> {
    this.isRequesting = true
    const options = getOptions.call(this)

    return this.transport.read(options)
      .then(this.processResponse.bind(this))

    function getOptions () {
      return {
        top: toJS(this.$top),
        orderby: toJS(this.$orderby),
        select: toJS(this.$select),
        expand: toJS(this.$expand),
        filter: toJS(this.$filter)
      }
    }
  }

  readMore () {
    if (!this.canReadMore) {
      return false
    }
    this.isRequesting = true
    return this.transport.read({ nextLink: toJS(this.$nextLink) })
      .then(this.processResponse.bind(this))
  }

  remove (item: any): void {
    if (this.data.find((ent) => item.Id === ent.Id)) {
      item.Recycled = true
    }
  }

  // todo: Error handling
  sync (): Promise<*> | void {
    if (!this.hasChanges) {
      return Promise.resolve()
    }

    this.isRequesting = true
    const promises = []
    const dataCreated = this.data.filter((field) => field.isNew)
    const dataUpdated = this.data.filter((field) => field.isDirty && !field.Recycled && !field.hasOwnProperty('isNew'))
    const dataRecycled = this.data.filter((field) => field.Recycled)

    if (dataCreated.length) {
      dataCreated.forEach((item) => {
        item.submit()
        let data = toJS(item.model)
        delete data.isNew
        delete data.Id
        let serializedData = this.serialize(data)
        const promise = this.transport.create(serializedData)
        promises.push(promise)

        promise.then(action('created', (data) => {
          const itemVM = createViewModel(observable(data))
          const index = this.data.findIndex(i => i === item)
          this.data.splice(index, 0, itemVM)
          this.data.remove(item)
        }).bind(this)).catch((error) => {
          console.log('error', error)
        })
      })
    }

    if (dataUpdated.length) {
      dataUpdated.forEach((item) => {
        item.submit()
        let data = toJS(item.model)
        let serializedData = this.serialize(data)
        const promise = this.transport.update(serializedData)
        promises.push(promise)

        promise.then(action('updated', (data) => {
          const itemVM = createViewModel(observable(data))
          const index = this.data.findIndex(i => i === item)
          this.data.splice(index, 0, itemVM)
          this.data.remove(item)
        }).bind(this)).catch((error) => {
          console.log('error', error)
        })
      })
    }

    if (dataRecycled.length) {
      dataRecycled.forEach((item) => {
        const promise = this.transport.destroy(item.Id)
        promises.push(promise)

        promise.then(action('destroyed', (data) => {
          this.data.remove(item)
        }).bind(this)).catch((error) => {
          console.log('error', error)
        })
      })
    }

    return Promise.all(promises)
      .then(action(() => {
        this.isRequesting = false
      }))
      .catch(action(() => {
        this.isRequesting = false
      }))
  }
}



