// @flow
import AjaxClient from './AjaxClient'
import { createModel } from './createModel'
const cache = {}
const headerDefaults = {
  accept: 'application/json;odata.metadata=minimal'
}

export class ListModelManager {
  appId: string
  headers: Object
  proxy: AjaxClient
  listModels = {}

  constructor (appId: string) {
    this.appId = appId
    this.headers = Object.assign({}, headerDefaults, { appId })
    this.proxy = new AjaxClient()
  }

  getListModel (connId: string, listId: string) {
    if (this.listModels[ listId ]) {
      return Promise.resolve(this.listModels[ listId ])
    }

    const url = `/odata/Connections('${connId}')/Lists('${listId}')?$expand=Fields,RelatedChildren($expand=Fields)`
    const request = Object.assign({}, {
      url,
      headers: this.headers
    })

    const { promise } = this.proxy.read(request)

    return promise.then((resp) => {
      if (resp && resp.hasOwnProperty('Fields')) {
        this.listModels[ listId ] = createModel(resp.Fields)
        return this.listModels[ listId ]
      }

      return {}
    })
  }

  importFields (listId: string, fields: Array<Object>) {
    // Todo Consider overwrite option
    if (this.listModels[ listId ]) {
      return this
    }

    this.listModels[ listId ] = createModel(fields)
    return this.listModels[ listId ]
  }
}

/**
 * Create a ListModelManager instance
 */
export function createListModelManager (appId: string): ListModelManager {
  if (cache[ appId ]) {
    return cache[ appId ]
  }

  cache[ appId ] = new ListModelManager(appId)

  return cache[ appId ]
}



