// @flow
import { extendObservable, action } from 'mobx'
import AjaxClient from './AjaxClient'
import { parameterMap } from './parameterMap'

const headerDefaults = {
  accept: 'application/json;odata.metadata=minimal'
}
// todo: Concurrency handling Etag If-Match header
export class Transport {
  counter: {
    create: number,
    update: number,
    read: number,
    destroy: number
  }
  proxy: AjaxClient
  baseUrl: string
  headers: Object

  constructor (appid: string, baseUrl: string) {
    this.proxy = new AjaxClient(baseUrl)
    this.baseUrl = baseUrl
    this.headers = Object.assign({}, headerDefaults, { appid })
    extendObservable(this, {
      request: null,
      error: null,
      counter: {
        create: 0,
        update: 0,
        read: 0,
        destroy: 0
      },
      read: action(this.read),
      update: action(this.update),
      create: action(this.create),
      destroy: action(this.destroy),
      // alias
      post: action(this.create)
    })
  }

  create (data?: {} = {}) {
    const url = `${this.baseUrl}`
    const request = Object.assign({}, {
      url,
      headers: this.headers,
      data: JSON.stringify(data)
    })

    const { promise } = this.proxy.create(request)
    this.counter.create++
    return promise
      .then(action(() => this.counter.create--))
      .then(() => promise)
  }

  read (data?: {nextLink?: string} = {}) {
    const url = getUrl(this.baseUrl, data)
    const request = Object.assign({}, {
      url,
      headers: this.headers
    })

    const { promise } = this.proxy.read(request)
    this.counter.read++
    return promise
      .then(action(() => this.counter.read--))
      .then(() => promise)

    function getUrl (baseUrl, params: {} = {}) {
      // $FlowFixMe https://github.com/facebook/flow/issues/1805
      const strParams = params.hasOwnProperty('nextLink') ? params.nextLink : parameterMap('read', params)

      if (!strParams) {
        return baseUrl
      }
      const cleanBaseUrl = baseUrl.split('?')[ 0 ]

      return `${cleanBaseUrl}?${strParams}`
    }
  }

  update (data: { Id: string }) {
    const url = `${this.baseUrl}('${data.Id}')`
    const request = Object.assign({}, {
      url,
      headers: this.headers,
      data: JSON.stringify(data)
    })

    const { promise } = this.proxy.update(request)
    this.counter.update++
    return promise
      .then(action(() => this.counter.update--))
      .then(() => promise)
  }

  destroy (id: string) {
    const url = `${this.baseUrl}('${id}')`
    const request = Object.assign({}, {
      url,
      headers: this.headers
    })

    const { promise } = this.proxy.destroy(request)
    this.counter.destroy++
    return promise
      .then(action(() => this.counter.destroy--))
      .then(() => promise)
  }
}

export function createTransport (AppId: string, baseUrl: string) {
  return new Transport(AppId, baseUrl)
}



