// @flow
import { enums } from 'tcomb'

export const SortDir = enums.of(['asc', 'desc'], 'SortDir')

export type SortExpression = {
  dir: SortDir,
  field: string
};

export type Sort = Array<SortExpression>

export type CollectionOptions = {
  expand?: Array<string>,
  select?: Array<string>,
  orderby?: Sort,
  filter?: {},
  top?: number,
  // Todo Consider using a more precise function def
  parse?: Function,
  serialize?: Function
}

export type ApiCall = {
  abort: () => void;
  promise: Promise<*>;
}

export type FilterExpression = {
  field: string,
  operator: string,
  value: any
}

export const FilterLogic = enums.of(['or', 'and'], 'FilterLogic')

// recursive
export type FilterGroup = {
  logic: FilterLogic,
  filters: Array<FilterExpression | FilterGroup>
}

export type AjaxClient = {
  read(options?: {}): ApiCall;
  create(options?: {}): ApiCall;
  update(options?: {}): ApiCall;
  destroy(options?: {}): ApiCall;
}



