// @flow
import Collection from '../../api/Collection'
import type { CollectionOptions } from '../../api/types'

class ConnectionsStore extends Collection {

  constructor (appId: string, options?: CollectionOptions = {}) {
    const url = `/odata/Connections`
    super(appId, url, options)
  }
}

export function createConnectionsStore (appId: string, options?: CollectionOptions = {}) {
  return new ConnectionsStore(appId, options)
}



