// @flow
import Collection from '../../api/Collection'
import type { CollectionOptions } from '../../api/types'

class GridConfigsStore extends Collection {

  constructor (appId: string, options?: CollectionOptions = {}) {
    const url = `/odata/GridConfigs`
    super(appId, url, options)
  }
}

export function createGridConfigsStore (appId: string, options?: CollectionOptions = {}) {
  return new GridConfigsStore(appId, options)
}



