// @flow
import _ from 'lodash'
import type { Sort, SortDir, SortExpression } from '../types'

export function normalizeSort (options: Sort | SortExpression | string, dir: SortDir = 'asc') {
  if (typeof options === 'string') {
    return [ { field: options, dir: dir } ]
  }
  if (_.isPlainObject(options)) {
    return [ options ]
  }
  return options
}



