// @flow
import Collection from '../../api/Collection'
import type { CollectionOptions } from '../../api/types'

class ActionConfigsStore extends Collection {

  constructor (appId: string, options?: CollectionOptions = {}) {
    const url = `/odata/ActionConfigs`
    super(appId, url, options)
  }
}

export function createActionConfigsStore (appId: string, options?: CollectionOptions = {}) {
  return new ActionConfigsStore(appId, options)
}



