// @flow
import Collection from '../../api/Collection'
import type { CollectionOptions } from '../../api/types'
import { action, extendObservable } from 'mobx'

const optionsDefaults = {
  orderby: [ { field: 'AppConfig/ApplicationName', dir: 'asc' } ],
  expand: [ 'AppConfig($select=AppId,ApplicationName,ModifiedBy,ModifiedDate)' ],
  filter: {
    logic: 'and',
    filters: [
      { field: 'UserApplication ', operator: 'ne', value: null },
      { field: 'UserApplication/IsInvitation', operator: 'ne', value: true }
    ]
  }
}

class ApplicationsStore extends Collection {

  constructor (options?: CollectionOptions = {}) {
    const url = `/odata/Applications`
    super('', url, options)

    extendObservable(this, {
      getUserApplication: action(this.getUserApplications)
    })
  }

  getUserApplications () {
    return this.query(optionsDefaults)
  }
}

export function createApplicationsStore (options?: CollectionOptions) {
  return new ApplicationsStore(options)
}



