// @flow
import { odataFieldMap } from '../maps/'

export function createModel (fields: any) {
  const result = {
    fieldMap: {},
    fieldOption: {},
    defaultModelValues: {
      Title: ''
    }
  }

  fields.forEach((field) => {
    const id = field.StaticName
    const type = field.FieldType

    if (!id || !type) {
      return
    }

    result.fieldMap[ id ] = field
    if (odataFieldMap.hasOwnProperty(type)) {
      result.fieldOption[ id ] = odataFieldMap[ type ]
    }
    result.defaultModelValues[ id ] = checkForModelValue(id, type, field)
  })

  return result

  function checkForModelValue (id, type, field) {
    let value = null
    if (odataFieldMap.hasOwnProperty(type) && odataFieldMap[ type ].hasOwnProperty('defaultValue')) {
      value = odataFieldMap[ type ].defaultValue
    }
    if (type === 'Text') {
      value = ''
    }
    return value
  }
}



