// @flow
import _ from 'lodash'
import { toOdataFilter } from './toODataFilter'

const mappers = {
  pageSize: _.noop,
  page: _.noop,
  filter: (params, filter) => {
    let expr
    if (filter && filter.filters && filter.filters.length > 0) {
      expr = toOdataFilter(filter)
      params.push(`$filter=${expr}`)
    }
  },
  orderby: (params, orderby) => {
    const expr = orderby.map((value) => {
      let order = _.trim(value.field)

      if (value.dir === 'desc') {
        order += ' desc'
      }

      return order
    }).join(',')

    if (expr) {
      params.push(`$orderby=${expr}`)
    }
  },
  select: (params, select) => {
    const expr = select.map((value) => _.trim(value))
    if (expr.length) {
      if (expr.indexOf('Id') === -1) {
        expr.unshift('Id')
      }

      params.push(`$select=${expr.join(',')}`)
    }
  },
  top: (params, top) => {
    if (top !== -1) {
      params.push(`$top=${top}`)
    }
  },
  expand: (params, expand) => {
    const expr = expand.map((value) => _.trim(value))
    if (expr.length) {
      params.push(`$expand=${expr.join(',')}`)
    }
  }
}

export function parameterMap (type: string = 'read', options?: {} = {}) {
  const params = []

  if (type === 'read') {
    for (const key of Object.keys(options)) {
      const val = options[ key ]
      if (mappers[ key ]) {
        if (key === 'filter' || key === 'orderby') {
          mappers[ key ](params, val)
        } else {
          mappers[ key ](params, val)
        }
      } else {
        params[ key ] = val
      }
    }
  }

  return params.length ? params.join('&') : ''
}




