// @flow
import Collection from '../../api/Collection'
import type { CollectionOptions } from '../../api/types'

class AuditsStore extends Collection {

  constructor (options?: CollectionOptions = {}) {
    const url = `/odata/Audits`
    super('', url, options)
  }
}

export function createAuditsStore (options?: CollectionOptions = {}) {
  return new AuditsStore(options)
}



