import _ from 'lodash'
import * as fn from './fn'
const odataFilters = {
  eq: 'eq',
  neq: 'ne',
  gt: 'gt',
  gte: 'ge',
  lt: 'lt',
  lte: 'le',
  in: 'in',
  ne: 'ne',
  contains: 'contains',
  doesnotcontain: 'doesnotcontain',
  endswith: 'endswith',
  startswith: 'startswith',
  isnull: 'eq',
  null: 'eq',
  nnull: 'ne',
  isnotnull: 'ne',
  isempty: 'eq',
  isnotempty: 'ne'
}
const nullCheck = [ 'null', 'nnull', 'isnull', 'isnotnull' ]
const emptyCheck = [ 'isempty', 'isnotempty' ]

// V1: No field specific filter handling. No replacements
export function toOdataFilter (filter) {
  const result = []
  const logic = filter.logic || 'and'
  const filters = filter.filters
  let idx
  let length
  let field
  let format
  let operator
  let value

  for (idx = 0, length = filters.length; idx < length; idx++) {
    filter = filters[ idx ]

    if (filter.filters) {
      filter = toOdataFilter(filter)
    } else {
      // Move null check before replacementValues
      if (_.indexOf(emptyCheck, filter.operator.toLowerCase()) > -1) {
        filter.value = ''
      }
      if (_.indexOf(nullCheck, filter.operator.toLowerCase()) > -1) {
        filter.value = null
      }
      operator = filter.operator
      field = filter.field
      value = filter.value
      field = field.replace(/\./g, '/')
      filter = odataFilters[ operator ]

      if (filter && value !== undefined) {
        if (_.isString(value)) {
          format = "'{1}'"

          value = value.replace(/'/g, "''")
          value = encodeURIComponent(value)
          // addresses overencoding of certain characters eg. whitespace %20 -> %2520
          value = value.replace(/%20/g, ' ')
          value = value.replace(/%3A/g, ':')
          value = value.replace(/%2F/g, '/')
        } else if (_.isNumber(value)) {
          format = '{1}'
        } else if (_.isDate(value) && operator !== 'eq') {
          value = value.toISOString()
          format = '{1}'
        } else {
          format = '{1}'
        }

        if (filter.length > 3) {
          if (filter !== 'substringof') {
            if (operator === 'doesnotcontain') {
              format = 'not contains({2},' + format + ')'
            } else {
              format = '{0}({2},' + format + ')'
            }
          } else {
            format = '{0}(' + format + ',{2})'
          }
        } else {
          format = '{2} {0} ' + format
        }

        // "in" operator expansion
        if (filter === 'in' && Array.isArray(value)) {
          format = '({1})'
          value = expandInFilter(field, value)
        }

        filter = fn.format(format, filter, value, field)

        // overwrite filter if it's a lambda expression

        // if (lambda) {
        //
        //   filter = createLambdaExpression(lambda, logic, odataFilters[ operator ], value, field)
        // }

        // overwrite filter for DateTime eq filter

        // if (operator === 'eq' && (fieldMap[ field ] && fieldMap[ field ].FieldType === 'DateTime')) {
        //
        //   filter = createDateEqualFilter(field, value)
        //
        // }
      }
    }

    result.push(filter)
  }

  filter = result.join(' ' + logic + ' ')

  if (result.length > 1) {
    filter = '(' + filter + ')'
  }

  return filter
}

function expandInFilter (field, value) {
  const result = []
  const logic = 'or'

  value.forEach((orFilter) => {
    result.push(`${field} eq '${orFilter}'`)
  })

  return result.join(' ' + logic + ' ')
}



