// flow
import _ from 'lodash'
import { extendObservable, toJS, action, ObservableMap } from 'mobx'
import { createTransport } from './../api/Transport'

// Perform aggregation request
// This is a kind of one time store
class AggregationRequest {
  _pristineResponse: any
  _pristineData: [];
  isRequesting: boolean
  data: ObservableMap
  appId: string
  baseUrl: string
  config: Object

  constructor (appId: string, config: Object, format: string) {
    this.baseUrl = config.Group.hasOwnProperty('SubGroupFields') ? '/api/Aggregation/MultiSeries' : '/api/Aggregation'
    this.transport = createTransport(appId, this.baseUrl)

    this.appId = appId
    this.data = []
    this._pristineData = []
    this.config = config
    this.toJS = toJS

    extendObservable(this, {
      isRequesting: true,
      read: action(this.read),
      processResponse: action(this.processResponse)
    })
  }

  parse (resp: any) {
    return resp
  }

  serialize (data: any) {
    return data
  }

  processResponse (resp: any) {
    this._pristineResponse = _.cloneDeep(resp)
    const data = _.isArray(resp) ? resp : [ resp ]
    const parsedData = this.parse(data)
    this._pristineData = _.cloneDeep(parsedData)
    this.isRequesting = false
    this.data = parsedData

    // return the store
    return this
  }

  read (): Promise<*> {
    this.isRequesting = true
    return this.transport.post(this.config)
      .then(this.processResponse.bind(this))
  }
}

export function createAggregationRequest (appId: string, config: Object, format: string) {
  return new AggregationRequest(appId, config, format)
}



