import { DEFAULT_CONNECTION_ID } from '../constants'
import { createTransformer, toJS } from 'mobx'
import _ from 'lodash'

/**
 * Un-opinionated conversion of OData Responses
 * Minimal conversion and consistency checking of Odata responses
 * In place conversion of stringified JSON properties
 * Use AppRequests this.stores to specify Select and Expand options to minimize response
 */

const storesTransformer = createTransformer((stores) => ({
  actions: stores.actions.data.map(actionTransformer),
  app: stores.app.data.map(appTransformer),
  charts: stores.charts.data.map(chartTransformer),
  connections: stores.connections.data.map(connectionTransformer),
  grids: stores.grids.data.map(gridTransformer),
  layouts: stores.lists.data.map(layoutTransformer),
  libraries: stores.lists.data.map(libraryTransformer),
  lists: stores.lists.data.map(listTransformer),
  views: stores.views.data.map(viewTransformer)
}))

export default storesTransformer

const actionTransformer = createTransformer((action) => {
  const model = toJS(action.model)

  model.ActionConfigJSON = JSON.parse(model.ActionConfigJSON)
  // Consistency check
  model.Connection = model.ConnectionId || DEFAULT_CONNECTION_ID

  return model
})

const appTransformer = createTransformer((app) => {
  return toJS(app.model)
})

const chartTransformer = createTransformer(chart => {
  const model = toJS(chart.model)
  model.ChartConfigJSON = JSON.parse(model.ChartConfigJSON)
  model.DataSourceConfigJSON = JSON.parse(model.DataSourceConfigJSON)
  model.ConnectionId = model.ConnectionId || model.ChartConfigJSON.ConnectionId || DEFAULT_CONNECTION_ID
  model.ListId = model.ListId || model.ChartConfigJSON.DataSource

  return model
})

const connectionTransformer = createTransformer((conn) => {
  return toJS(conn.model)
})

const gridTransformer = createTransformer((grid) => {
  const model = toJS(grid.model)
  model.GridConfigJSON = JSON.parse(model.GridConfigJSON)
  model.ConnectionId = model.ConnectionId || model.GridConfigJSON.ConnectionId || DEFAULT_CONNECTION_ID
  model.ListId = model.ListId || model.GridConfigJSON.DataSource
  model.ViewFields = _.without(model.GridConfigJSON.ViewFields, null)

  return model
})

const layoutTransformer = createTransformer((layout) => {
  return toJS(layout.model)
})

const libraryTransformer = createTransformer((list) => {
  return toJS(list.model)
})

const listTransformer = createTransformer((list) => {
  return toJS(list.model)
})

const viewTransformer = createTransformer((view) => {
  return toJS(view.model)
})



