// @flow
import Collection from '../../api/Collection'
import { when, computed } from 'mobx'
import { createListModelManager, ListModelManager } from '../../api/ListModelManager'
import { DEFAULT_CONNECTION_ID } from '../../constants'
import type { CollectionOptions } from '../../api/types'

class ListsStore extends Collection {
  connectionId: string
  listModelManager: ListModelManager

  constructor (appId: string, connectionId: string, options?: CollectionOptions = {}) {
    const url = `/odata/Connections('${connectionId}')/Lists`
    super(appId, url, options)
    this.connectionId = connectionId
    this.listModelManager = createListModelManager(appId)
    const hasFields = computed(() => {
      if (this.data.length < 1) {
        return false
      }

      return this.data[0].Fields && Array.isArray(this.data[0].Fields.slice())
    })
    when(
      () => hasFields, () => {
        this.data.forEach((list) => {
          this.listModelManager.importFields(list.Id, list.Fields.slice())
        })
      })
  }

}

export function createListsStore (appId: string, connectionId?: string = DEFAULT_CONNECTION_ID, options?: CollectionOptions = {}) {
  return new ListsStore(appId, connectionId, options)
}



