import { useStrict } from 'mobx'
import * as stores from './stores'
import { createAppRequest } from './app/AppRequest'
import { createAggregationRequest } from './Aggregation/AggregationRequest'
import { createListModelManager } from './api/ListModelManager'

// mobx.extras.shareGlobalState()

useStrict(true)

if (__DEV__) { // eslint-disable-line
  const enableLogging = require('mobx-logger').enableLogging
  enableLogging()
}

export default {
  stores,
  createAppRequest,
  createAggregationRequest,
  createListModelManager,
  VERSION: VERSION // eslint-disable-line
}



